import React, { FC, PropsWithChildren, ReactNode } from 'react'

import CloseIcon from '@mui/icons-material/Close'
import Box from '@mui/material/Box'
import IconButton from '@mui/material/IconButton'
import Modal from '@mui/material/Modal'

import { StyleSXPropsType, SuperButton } from '../SuperButton/SuperButton'

import s from './SuperModal.module.scss'

const style = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
}

type ModalPropsType = {
  title: string | ReactNode
  modalHeader: string
  open: boolean
  handleClose: () => void
  handleOpen: () => void
  styleSX?: StyleSXPropsType
  disabledButton?: boolean
  disabledSaveButton?: boolean
}

export const SuperModal: FC<PropsWithChildren<ModalPropsType>> = ({
  children,
  title,
  modalHeader,
  open,
  handleClose,
  handleOpen,
  disabledButton,
  disabledSaveButton,
}) => {
  return (
    <div>
      {typeof title === 'string' ? (
        <SuperButton title={title} onClick={handleOpen} disabled={disabledSaveButton} />
      ) : (
        <IconButton onClick={handleOpen} disabled={disabledButton}>
          {title}
        </IconButton>
      )}
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <h5 className={s.header}>
            {modalHeader}
            <span className={s.close}>
              <IconButton>
                <CloseIcon onClick={handleClose} />
              </IconButton>
            </span>
          </h5>
          {children}
        </Box>
      </Modal>
    </div>
  )
}
